.calendar {
  display: flex;
  flex-wrap: wrap;
  gap: $gap;

  min-width: 8 * $daySize;

  @for $d from 28 through 31 {
    &--month-length-#{$d} :nth-child(n + #{$d + 1}) {
      display: none;
    }
  }

  @each $day-name, $day-index in $weekdays {
    &--start-day-#{$day-name} :first-child {
      margin-left: ($daySize + $gap) * $day-index;
    }
  }

  &__day {
    @for $i from 1 through $mounthLength {
      &:nth-child(#{$i})::before {
        content: '#{$i}';
      }
    }

    box-sizing: border-box;

    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    gap: $gap;
    height: $daySize;
    width: $daySize;
    border: 1px solid black;
    background-color: #eee;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 30px;
    cursor: pointer;
    transition:
      transform 0.5s,
      background-color 0.5s;

    &:hover {
      transform: translateY(-20px);
      background-color: #ffbfcb;
    }
  }
}
